import React, {Component} from "react";
import {integer, fraction} from "../../../../magnum2/components/Utils";
import OOVMonthlyPriceTag from "eshop/modules/simfree/components/OOVMonthlyPriceTag"
import {formatPrice} from "./MagnumOfferPropositionListItem";
import {isLTE} from "../../../../magnum2/components/BundleTypeUtils";

const MagnumPropositionListPriceTag = (props) =>{
    if(!props.offer)
        return (null);

    let monthlyPaymentsObj = [];
    if (isLTE(props.availableBundleTypes) && props.offer.monthlyPayments[0]) {
        let monthTo = Math.max(...props.offer.monthlyPayments.map(mp => mp.endCycle), 0);
        let monthlyPayment = props.offer.monthlyPayments[0];
        monthlyPaymentsObj.push({
            currency: props.offer.currency,
            description: "",
            monthFrom: monthlyPayment.startCycle,
            monthTo: monthTo,
            netPrice: monthlyPayment.finalNetPrice,
            originalGrossPrice: monthlyPayment.finalPrice,
            originalNetPrice: monthlyPayment.finalNetPrice,
            originalPrice: null,
            price: integer(monthlyPayment.finalPrice) + ',' + fraction(monthlyPayment.finalPrice)
        });
    } else {
        props.offer.monthlyPayments.map(t=>{
            monthlyPaymentsObj.push({
                currency: props.offer.currency,
                description: "",
                monthFrom: t.startCycle,
                monthTo: t.endCycle === t.startCycle ? t.endCycle + 1: t.endCycle ,
                netPrice: t.finalNetPrice,
                originalGrossPrice: t.finalPrice,
                originalNetPrice: t.finalNetPrice,
                originalPrice: null,
                price: integer(t.finalPrice) + ',' + fraction(t.finalPrice)}
            )});
    }

    let monthlyPayments = formatPrice(props.offer.monthlyPayments[0].finalPrice, props.offer.currency);
    return (

        <OOVMonthlyPriceTag
            baseSummaryPrice={monthlyPayments}
            convSummaryPrice={monthlyPayments}
            monthlyPayments={monthlyPaymentsObj}
            monthlyPaymentsWithBonuses={monthlyPaymentsObj}
            clientContext={props.clientContext}
            loyaltyLength={props.loyaltyLength}
            monthString={props.monthlyString||"mies."}
            mainPriceClass={props.mainPriceClass}
            alignClass={props.alignClass}
            offerDiscountInfoDescription={props.offerDiscountInfoDescription}
            offerDiscountInfoConvDescription={props.offerDiscountInfoConvDescription}
            baseId={props.baseId}
            isMagnum={true}
        />

    )
}
export default MagnumPropositionListPriceTag;